import React from "react";

export default function Form(props) {
  const [quantity, setQuantity] = React.useState(0);

  const { movie } = props;

  return (
    <form>
      <label>
        <p>{movie.name}</p>
      </label>
      <button
        type="button"
        onClick={() => setQuantity(quantity - 1)}
        disabled={quantity <= 0}
      >
        -
      </button>
      {quantity}
      <button
        type="button"
        onClick={() => setQuantity(quantity + 1)}
        disabled={quantity >= movie.available}
      >
        +
      </button>
    </form>
  );
}
